import React, { useEffect, useState } from "react";
import style from "./IDCardSetting.module.css";
import jszip from "jszip";
import JsBarcode from "jsbarcode";
import { saveAs } from "file-saver";
import { useTranslation } from "react-i18next";
import { TextInput } from "../../../../Authentication/Auth_New_Flow/InputWithoutIcon/InputWithoutIcon";
import JoinFormFileEdit from "../../../../JoiningForm/Student/Form/JoinFormFileEdit";
import { useFileUpload } from "../../../../hooks/authentication-api";
import {
  useInsIDCardData,
  useStaffExportData,
  useUpdateInsIdCardFields,
} from "../../../../hooks/ExportData/export-data-api";
import PdfIdCardPortrait from "../../../../pdf/idcard/pdf-id-card-portrait";
import PdfIdCardPortrait2 from "../../../../pdf/idcard/pdf-id-card-portrait2";
import PdfIdCardPortrait3 from "../../../../pdf/idcard/pdf-id-card-portrait3";
import PdfIdCardLandscape from "../../../../pdf/idcard/pdf-id-card-landscape";
import StudentIdCard from "../../../../MembersTab/staff/Mainbody/IdCardSection/StudentIdCard";
import { imageShowUrl, imageShowUrl1 } from "../../../../services/BaseUrl";
import JoinFormCalender from "../../../../JoiningForm/Student/Form/JoinFormCalender";
import ColorPicker from "./ColorPicker";
import QLoader from "../../../../Loader/QLoader";

function IDCardSetting({ instituteId, status }) {
  const { t } = useTranslation();
  const [oneFileUpload] = useFileUpload();
  const { staffExportData } = useStaffExportData();
  const [updateInsIdCardFields] = useUpdateInsIdCardFields();
  const [disabled, setDisabled] = useState(false);
  const [index, setIndex] = useState(0);
  const [color, setColor] = useState("#FFFFFF");
  const [open, setOpen] = useState(false);
  const [formatindex, setFormatIndex] = useState(null);
  const [idCardData, setIdCardData] = useState({
    institute_bg_cover: "",
    institute_logo: "",
    affiliated_logo: "",
    authority_signature: "",
    name: "",
    affiliated: "",
    address: "",
    issue_date: "",
    valid_date: "",
  });

  const { insIdCardData, insIdCardDataRefetch } = useInsIDCardData({
    id: instituteId,
    skip: !instituteId,
  });

  useEffect(() => {
    if (instituteId) {
      insIdCardDataRefetch();
    }
  }, [instituteId, insIdCardDataRefetch]);
  const onFileUpload = (e) => {
    if (e.target.files?.length) {
      setDisabled((pre) => !pre);
      const formData = new FormData();
      formData.append("file", e.target.files[0]);
      oneFileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          if (res.data?.imageKey) {
            setIdCardData((prev) => ({
              ...prev,
              [e.target.name]: res.data?.imageKey,
            }));
          }
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  useEffect(() => {
    setIdCardData({
      institute_bg_cover:
        insIdCardData?.institute_id_setting?.institute_bg_cover,
      institute_logo: insIdCardData?.institute_id_setting?.institute_logo,
      affiliated_logo: insIdCardData?.institute_id_setting?.affiliated_logo,
      name: insIdCardData?.institute_id_setting?.name ?? "",
      affiliated: insIdCardData?.institute_id_setting?.affiliated ?? "",
      address: insIdCardData?.institute_id_setting?.address ?? "",
      authority_signature:
        insIdCardData?.institute_id_setting?.authority_signature,
      issue_date: insIdCardData?.institute_id_setting?.issue_date,
      valid_date: insIdCardData?.institute_id_setting?.valid_date,
    });
  }, [insIdCardData]);

  useEffect(() => {
    validateObj(idCardData);
  }, [idCardData]);

  const validateObj = (object) => {
    return Object.values(object)?.every((v) => v !== "" && v !== undefined);
  };

  function getfunctionEntries(arr) {
    return arr?.map(function (e) {
      return {
        GRNo: e?.staffROLLNO,

        Name:
          e?.staffFirstName + " " + e?.staffMiddleName + " " + e?.staffLastName,
        Gender: e?.staffGender ? e?.staffGender : "NA",
        MotherName: e?.staffMotherName ? e?.staffMotherName : "NA",
        DOB: e?.staffDOB ? e?.staffDOB : "NA",
        CastCategory: e?.staffCastCategory ? e?.staffCastCategory : "NA",
        Religion: e?.staffReligion ? e?.staffReligion : "NA",
        BirthPlace: e?.staffBirthPlace ? e?.staffBirthPlace : "NA",
        Address: e?.staffAddress ? e?.staffAddress : "NA",
        PhoneNumber: e?.staffAddress ?? "NA",
        State: e?.staffState ? e?.staffState : "NA",
        District: e?.staffDistrict ? e?.staffDistrict : "NA",
        Nationality: e?.staffNationality ? e?.staffNationality : "NA",
        Email: e?.staffEmail ?? "NA",
        Contact: e?.staffPhoneNumber ? e?.staffPhoneNumber : "NA",
        JoiningDate: e?.staffJoinDate ? e?.staffJoinDate?.split("T")[0] : "NA",
        AadharNumber: e?.staffAadharNumber ? e?.staffAadharNumber : "NA",
        Profilephoto: e?.staffProfilePhoto,
        Designation: e?.recentDesignation ?? "Staff",
        PAN: e?.staffPanNumber ?? "NA",
      };
    });
  }

  const handleUpdateInfo = () => {
    setDisabled(true);
    const data = {
      institute_id_setting: idCardData,
    };
    updateInsIdCardFields({
      idcard: data,
      id: instituteId,
    })
      .then((res) => {
        // insIdCardDataRefetch();
        setDisabled(false);
        setIndex(1);
      })
      .catch({});
  };
  const staffData = getfunctionEntries(staffExportData?.staff_card);
  const handlePdf = async (studentData, department) => {
    const zip = new jszip();

    const institute = {
      insbg: `${imageShowUrl1}/${idCardData?.institute_bg_cover}`,
      insLogo: `${imageShowUrl1}/${idCardData?.institute_logo}`,
      affliatedImg: `${imageShowUrl1}/${idCardData?.affiliated_logo}`,
      insName: idCardData?.name,
      insAffiliation: idCardData?.affiliated,
      insAddress: idCardData?.address,
      signImg: idCardData?.authority_signature,
    };
    for (let i = 0; i < studentData?.length; i++) {
      const user = {
        userProfilePhoto: studentData?.[i]?.Profilephoto
          ? `${imageShowUrl}/${studentData?.[i]?.Profilephoto}`
          : "/images/pdf/default-user-img.jpg",
        userName: studentData?.[i]?.Name ?? "NA",
        userDesignation:
          status === "staff" ? studentData?.[i]?.Designation : "Student",
        userPhoneNumber: studentData?.[i]?.PhoneNumber ?? "NA",
        userMail: studentData?.[i]?.Email ?? "NA",
        userDob: studentData?.[i]?.DOB ?? "NA",
        userBloodGroup: studentData?.[i]?.["Blood Group"] ?? "NA",
        userPan: studentData?.[i]?.PAN ?? "NA",
        userAdhar: studentData?.[i]?.AadharNumber ?? "NA",
        userDept: department ?? "NA",
        userAddress: studentData?.[i]?.Address ?? "NA",
        userGRNumber: studentData?.[i]?.GRNo ?? "NA",
        batchYear: studentData?.[i]?.Batch ?? "",
        issueDate: idCardData?.issue_date ?? "NA",
        validDate: idCardData?.valid_date ?? "NA",
      };
      JsBarcode("#itf", studentData?.[i]?.GRNo ?? "", {
        format: "CODE128",
        text: "",
      });
      const barcodeimg = document.querySelector("img#itf");

      let downloadPdf =
        (await formatindex) === 0
          ? PdfIdCardPortrait(institute, user)
          : formatindex === 1
          ? PdfIdCardLandscape(institute, user, barcodeimg, color)
          : formatindex === 3
          ? PdfIdCardPortrait3(institute, user, barcodeimg, color)
          : PdfIdCardPortrait2(institute, user);
      zip.file(`${studentData?.[i]?.Name} ID-Card.pdf`, downloadPdf);
    }
    const content = await zip.generateAsync({ type: "blob" });
    saveAs(content, `idcard.zip`);
  };

  const handleFetchData = () => {
    if (status === "staff") {
      handlePdf(staffData);
    } else {
      validateObj(idCardData) && formatindex === 1
        ? setOpen(true)
        : validateObj(idCardData) && setIndex(2);
    }
  };

  return (
    <div className={style.idcardsetting}>
      {index === 0 && (
        <>
          <TextInput
            type="text"
            label={t("institute_name")}
            importantt={true}
            placeholder={t("institute_name")}
            value={idCardData.name}
            handleChange={(e) =>
              setIdCardData({ ...idCardData, name: e.target.value })
            }
            customStyleInput={{
              width: "100%",
              padding: "0.6rem",
              marginBottom: "0.5rem",
            }}
          />
          <TextInput
            type="text"
            label={t("aff_body")}
            importantt={true}
            placeholder={t("enter_affiliation")}
            value={idCardData.affiliated}
            handleChange={(e) =>
              setIdCardData({ ...idCardData, affiliated: e.target.value })
            }
            customStyleInput={{
              width: "100%",
              padding: "0.6rem",
              marginBottom: "0.5rem",
            }}
          />
          <TextInput
            type="text"
            label={t("institute_address")}
            importantt={true}
            placeholder={t("enter_your_institute_address")}
            value={idCardData.address}
            handleChange={(e) =>
              setIdCardData({ ...idCardData, address: e.target.value })
            }
            customStyleInput={{
              width: "100%",
              padding: "0.6rem",
              marginBottom: "0.5rem",
            }}
          />

          <div className={style.idsampleInputs}>
            <JoinFormCalender
              labelText={t("issue_date")}
              placeholder={t("issue_date")}
              name={"dob"}
              value={`${
                idCardData.issue_date ? idCardData.issue_date : t("issue_date")
              }`}
              onDateFunction={(value) => {
                let splitDate = value?.split("/");
                let date = `${splitDate[2]}-${splitDate[1]}-${splitDate[0]}`;
                setIdCardData({
                  ...idCardData,
                  issue_date: date,
                });
              }}
              customStyleContainer={{
                position: "relative",
                // marginTop: "0.5rem",
                height: "38px",
              }}
              customStyleInput={{
                height: "38px",
              }}
              datePickerPosition={{
                top: "-15rem",
                left: "30%",
              }}
              customStyleCaledarIcon={{
                bottom: "-27px",
              }}
              customStyleLabel={{
                marginTop: "7px",
              }}
            />
            <JoinFormCalender
              labelText={t("valid_date")}
              placeholder={t("valid_date")}
              name={"dob"}
              value={`${
                idCardData.valid_date ? idCardData.valid_date : t("valid_date")
              }`}
              onDateFunction={(value) => {
                let splitDate = value?.split("/");
                let date = `${splitDate[2]}-${splitDate[1]}-${splitDate[0]}`;
                setIdCardData({
                  ...idCardData,
                  valid_date: date,
                });
              }}
              customStyleContainer={{
                position: "relative",
                // marginTop: "0.5rem",
                height: "38px",
              }}
              customStyleInput={{
                height: "38px",
              }}
              datePickerPosition={{
                top: "-15rem",
                left: "30%",
              }}
              customStyleCaledarIcon={{
                bottom: "-27px",
              }}
              customStyleLabel={{
                marginTop: "7px",
              }}
            />
          </div>

          <JoinFormFileEdit
            labelText={t("institute_logo")}
            name={"institute_logo"}
            value={idCardData?.institute_logo}
            onChange={onFileUpload}
            customStyleContainer={{
              width: "100%",
            }}
          />

          <JoinFormFileEdit
            labelText={t("id_bg")}
            name={"institute_bg_cover"}
            value={idCardData.institute_bg_cover}
            onChange={onFileUpload}
            customStyleContainer={{
              width: "100%",
            }}
          />

          <JoinFormFileEdit
            labelText={t("affiliated_logo")}
            name={"affiliated_logo"}
            value={idCardData.affiliated_logo}
            onChange={onFileUpload}
            customStyleContainer={{
              width: "100%",
            }}
          />

          <JoinFormFileEdit
            labelText={t("auth_sign")}
            name={"authority_signature"}
            value={idCardData.authority_signature}
            onChange={onFileUpload}
            customStyleContainer={{
              width: "100%",
            }}
          />

          <div className={style.buttonInner}>
            <div
              className={style.button_main}
              style={{
                marginTop: "1.5vw",
                width: "100%",
                // cursor: validateObj(idCardData) ? "pointer" : "context-menu",
                backgroundColor: validateObj(idCardData)
                  ? "#25594b"
                  : "#598f80",
              }}
              onClick={handleUpdateInfo}
              // onClick={() => validateObj(idCardData) && handleUpdateInfo()}
            >
              <span>{t("next")}</span>
            </div>
          </div>
        </>
      )}

      {index === 1 && (
        <>
          <div className={style.idsamples}>
            <div
              className={
                formatindex === 0
                  ? `${style.idsamplecard} ${style.idsamplecardActive}`
                  : style.idsamplecard
              }
              onClick={() => setFormatIndex(0)}
            >
              <img src="/images/pdf/id-portrait.png" alt="" />
            </div>
            <div
              className={
                formatindex === 1
                  ? `${style.idsamplecard} ${style.idsamplecardActive}`
                  : style.idsamplecard
              }
              onClick={() => setFormatIndex(1)}
            >
              <img src="/images/pdf/id-landscape.png" alt="" />
            </div>
            <div
              className={
                formatindex === 2
                  ? `${style.idsamplecard} ${style.idsamplecardActive}`
                  : style.idsamplecard
              }
              onClick={() => setFormatIndex(2)}
            >
              <img src="/images/pdf/id-portrait2.png" alt="" />
            </div>
            <div
              className={
                formatindex === 1
                  ? `${style.idsamplecard} ${style.idsamplecardActive}`
                  : style.idsamplecard
              }
              onClick={() => setFormatIndex(3)}
            >
              <img src="/images/pdf/id-portrait3.png" alt="" />
            </div>
          </div>

          {formatindex !== null && (
            <div className={style.buttoncontainer}>
              <div className={style.buttonInner}>
                <img id="itf" style={{ display: "none" }} alt="" />
                <div
                  className={style.button_main}
                  style={{
                    marginTop: "1.5vw",
                    width: "100%",
                    cursor: validateObj(idCardData)
                      ? "pointer"
                      : "context-menu",
                    backgroundColor: validateObj(idCardData)
                      ? "#25594b"
                      : "#598f80",
                  }}
                  onClick={handleFetchData}
                >
                  {status === "staff" ? (
                    <span>{t("download")}</span>
                  ) : (
                    <span>{t("next")}</span>
                  )}
                </div>
              </div>
            </div>
          )}
        </>
      )}

      {index === 2 && (
        <StudentIdCard
          instituteId={instituteId}
          handlePdf={(studentData) => handlePdf(studentData)}
        />
      )}

      <ColorPicker
        open={open}
        onclose={() => setOpen(false)}
        setColor={setColor}
        color={color}
        img={idCardData?.institute_bg_cover}
        setIndex={setIndex}
      />
      {disabled && <QLoader />}
    </div>
  );
}

export default IDCardSetting;
