import React, { useEffect } from "react";
import { SketchPicker } from "react-color";
import Dialog from "@mui/material/Dialog";
import style from "./IDCardSetting.module.css";
import { imageShowUrl } from "../../../../services/BaseUrl";
import { useTranslation } from "react-i18next";

function ColorPickerr({ open, onclose, color, setColor, img, setIndex }) {
  const { t } = useTranslation();
  const pickerStyle = {
    default: {
      picker: {
        position: "absolute",
        top: "0px",
        left: "160px",
      },
    },
  };
  return (
    <Dialog open={open}>
      <div className={style.colorpicker}>
        <div className={style.colorpickerheader}>
          <div>
            <h6>{t("select_bg_color")}</h6>
            <p>{t("id_color_match_note")}</p>
          </div>
          <img src="/images/close-post-icon.svg" alt="" onClick={onclose} />
        </div>
        <div
          className={style.colorpickerbody}
          style={{ backgroundColor: color }}
        >
          <SketchPicker
            styles={pickerStyle}
            color={color}
            onChange={(updatedColor) => setColor(updatedColor.hex)}
          />
          <button
            className={style.selectcorbtn}
            onClick={() => {
              setIndex(2);
              onclose();
            }}
          >
            {t("select")}
          </button>
        </div>
      </div>
    </Dialog>
  );
}

export default ColorPickerr;
