import jsPDF from "jspdf";

var doc = new jsPDF({
  orientation: "p",
  unit: "mm",
  format: [60, 90],
  // putOnlyUsedFonts:true,
  // floatPrecision: 16 // or "smart", default is 16
});

const PdfIdCardPortrait3 = (institute, user) => {
  var width = doc.internal.pageSize.getWidth();
  var height = doc.internal.pageSize.getHeight();

  doc.setFont("Helvetica", "normal", "700");

  doc.setLineWidth(25);
  doc.setDrawColor(92, 158, 92);
  doc.circle(width / 2 + 14, height / 2 + 11, 77);
  doc.circle(width / 2 - 14, height / 2 + 11, 77);
  doc.circle(width / 2 + 14, height / 2 - 11, 77);
  doc.circle(width / 2 - 14, height / 2 - 11, 77);
  doc.setLineWidth(0);

  doc.addImage(institute.insLogo, "JPEG", 8, 3, 11, 11);
  doc.addImage(institute.affliatedImg, "JPEG", width - 20, 3, 11.5, 11.5);

  doc.setLineWidth(3);
  doc.setDrawColor(255, 255, 255);
  doc.circle(13.47, 8.47, 6.8);
  doc.setLineWidth(0);

  doc.setLineWidth(3);
  doc.setDrawColor(255, 255, 255);
  doc.circle(width - 14, 8.47, 6.8);
  doc.setLineWidth(0);

  const affiliateLines = doc.splitTextToSize(institute?.insAffiliation, 70);
  doc.setFontSize(5.85);
  doc.setTextColor(28, 32, 43);
  if (affiliateLines.length > 1) {
    let verticalOffset = affiliateLines.length > 2 ? 15 : 16.5;
    doc.text(width / 2, verticalOffset, affiliateLines, "center");
    verticalOffset = affiliateLines.length + 17;
  } else {
    let verticalOffset = affiliateLines.length > 2 ? 15 : 16.5;
    doc.text(width / 2, verticalOffset, affiliateLines, "center");
    verticalOffset = affiliateLines.length + 17;
  }

  const textLines = doc.splitTextToSize(
    institute.insName,

    35
  );

  if (textLines.length > 1) {
    let verticalOffset = textLines.length > 2 ? 21.4 : 22.5;

    doc.setTextColor(227, 38, 54);
    doc.setFontSize(9.5);
    doc.text(width / 2, verticalOffset, textLines, "center");
    verticalOffset = textLines.length + 17;
  } else {
    let verticalOffset = textLines.length > 2 ? 16.4 : 17.5;

    doc.setTextColor(227, 38, 54);
    doc.setFontSize(9.5);
    doc.text(width / 2, verticalOffset, textLines, "center");
    verticalOffset = textLines.length + 17;
  }

  doc.setTextColor(28, 32, 43);
  doc.setFontSize(8.2);
  doc.text(width / 2, 30.5, `Academic Year ${user?.batchYear}`, "center");

  doc.addImage(user.userProfilePhoto, "JPEG", 21, 35, 19, 23);

  const nameLines = doc.splitTextToSize(
    user?.userName?.toUpperCase(),

    40
  );
  let verticalOffset = 63;
  doc.setFontSize(7.5);
  doc.setTextColor(227, 38, 54);
  if (nameLines.length > 1) {
    doc.text(width / 2, verticalOffset, nameLines, "center");
    verticalOffset = nameLines.length + 17;
  } else {
    doc.text(width / 2, verticalOffset, nameLines, "center");
    verticalOffset = nameLines.length + 17;
  }

  doc.setFontSize(7.5);
  doc.setTextColor(28, 32, 43);
  doc.text(width / 2, 70, user?.userDept, "center");

  doc.setFontSize(6.5);

  doc.setLineWidth(2);
  doc.setFillColor(40, 105, 46);
  doc.circle(7, 75, 2, "F");
  doc.circle(width / 2 + 4.5, 75, 2, "F");
  doc.circle(7, 81, 2, "F");
  doc.setLineWidth(0);
  doc.addImage(user.gricon, "JPEG", 6.25, 74, 2, 2);
  doc.addImage(user.dobicon, "JPEG", width / 2 + 3.5, 74, 2, 2);
  doc.addImage(user.dobicon, "JPEG", 6.25, 80, 2, 2);

  doc.text(10, 76, user?.userGRNumber);
  doc.text(width / 2 + 8, 76, user?.userDob);
  doc.text(10, 82, user?.userBloodGroup);

  //   doc.text(38, 66, user?. userBloodGroup);

  //   // ------------------------------------

  doc.addPage({
    orientation: "p",
    unit: "mm",
    format: [60, 90],
    // putOnlyUsedFonts:true,
    // floatPrecision: 16 // or "smart", default is 16
  });

  doc.setLineWidth(25);
  doc.setDrawColor(92, 158, 92);
  doc.circle(width / 2 + 14, height / 2 + 11, 77);
  doc.circle(width / 2 - 14, height / 2 + 11, 77);
  doc.circle(width / 2 + 14, height / 2 - 11, 77);
  doc.circle(width / 2 - 14, height / 2 - 11, 77);
  doc.setLineWidth(0);

  doc.addImage(institute.insLogo, "JPEG", 8, 3, 11, 11);
  doc.addImage(institute.affliatedImg, "JPEG", width - 20, 3, 11.5, 11.5);

  doc.setLineWidth(3);
  doc.setDrawColor(255, 255, 255);
  doc.circle(13.47, 8.47, 6.8);
  doc.setLineWidth(0);

  doc.setLineWidth(3);
  doc.setDrawColor(255, 255, 255);
  doc.circle(width - 14, 8.47, 6.8);
  doc.setLineWidth(0);

  doc.setFontSize(5.85);
  doc.setTextColor(28, 32, 43);
  if (affiliateLines.length > 1) {
    let verticalOffset = affiliateLines.length > 2 ? 15 : 16.5;
    doc.text(width / 2, verticalOffset, affiliateLines, "center");
    verticalOffset = affiliateLines.length + 17;
  } else {
    let verticalOffset = affiliateLines.length > 2 ? 15 : 16.5;
    doc.text(width / 2, verticalOffset, affiliateLines, "center");
    verticalOffset = affiliateLines.length + 17;
  }

  if (textLines.length > 1) {
    let verticalOffset = textLines.length > 2 ? 21.4 : 22.5;

    doc.setTextColor(227, 38, 54);
    doc.setFontSize(9.5);
    doc.text(width / 2, verticalOffset, textLines, "center");
    verticalOffset = textLines.length + 17;
  } else {
    let verticalOffset = textLines.length > 2 ? 16.4 : 17.5;

    doc.setTextColor(227, 38, 54);
    doc.setFontSize(9.5);
    doc.text(width / 2, verticalOffset, textLines, "center");
    verticalOffset = textLines.length + 17;
  }

  const addressLines = doc.splitTextToSize(
    institute?.insAddress,

    65
  );
  doc.setTextColor(18, 18, 18);
  doc.setFontSize(6);
  if (addressLines.length > 1) {
    let verticalOffset = textLines.length > 2 ? 32 : 30.5;
    doc.text(width / 2, verticalOffset, addressLines, "center");
    verticalOffset = addressLines.length + 17;
  } else {
    let verticalOffset = textLines.length > 2 ? 33 : 30.5;
    doc.text(width / 2, verticalOffset, addressLines, "center");
    verticalOffset = addressLines.length + 17;
  }

  doc.setTextColor(40, 105, 46);
  doc.setFontSize(9);
  doc.text(width / 2, 40.5, "Student Address", "center");

  doc.setTextColor(18, 18, 18);
  doc.setFontSize(10);
  //   doc.text(width / 2, 42, user.userAddress, "center");

  const userAddressLines = doc.splitTextToSize(
    user?.userAddress?.toUpperCase(),

    50
  );
  doc.setTextColor(44, 47, 55);
  doc.setFontSize(7.5);

  if (userAddressLines.length > 1) {
    let verticalOffset = 44.5;
    doc.text(width / 2, verticalOffset, userAddressLines, "center");
    verticalOffset = userAddressLines.length + 10;
  } else {
    let verticalOffset = 44.5;
    doc.text(width / 2, verticalOffset, userAddressLines, "center");
    verticalOffset = userAddressLines.length + 10;
  }
  doc.setTextColor(28, 32, 43);
  doc.setFontSize(6.5);

  doc.setLineWidth(2);
  doc.setFillColor(40, 105, 46);
  doc.circle(width / 2 - 12, 55, 2, "F");
  doc.circle(width / 2 - 12, 62, 2, "F");
  doc.setLineWidth(0);
  doc.addImage(user.phoneicon, "JPEG", width / 2 - 13, 54, 2, 2);
  doc.addImage(user.mailicon, "JPEG", width / 2 - 13, 61, 2, 2);

  doc.text(width / 2 - 8, 56, user?.userPhoneNumber);
  doc.text(width / 2 - 8, 63, user?.userMail);

  const carryText =
    "Always carry your card. In case of loss inform institute authority. If found please pass to given address";

  const carryTextLines = doc.splitTextToSize(carryText, 40);

  if (carryTextLines.length > 1) {
    let verticalOffset = 78;
    doc.text(width / 2, verticalOffset, carryTextLines, "center");
    verticalOffset = carryTextLines.length + 8;
  } else {
    let verticalOffset = 78;
    doc.text(width / 2, verticalOffset, carryTextLines, "center");
    verticalOffset = carryTextLines.length + 8;
  }

  // // let carryText
  // doc.text(5, 86.5, "Authorized Sign", "center");
  doc.output("dataurlnewwindow", `Student-Bonafide.pdf`);
};
export default PdfIdCardPortrait3;
