import jsPDF from "jspdf";

const PdfIdCardLandscape = (institute, user, barcodeimg, color) => {
  var doc = new jsPDF({
    orientation: "l",
    unit: "mm",
    format: [90, 60],
  });

  var width = doc.internal.pageSize.getWidth();
  var height = doc.internal.pageSize.getHeight();

  doc.setFont("Helvetica", "normal", "700");
  doc.addImage(institute.insbg, "JPEG", 0, 0, width, height);

  doc.addImage(institute.insLogo, "JPEG", 2, 3, 11, 11);
  doc.addImage(institute.affliatedImg, "JPEG", width - 13, 3, 11.5, 11.5);

  doc.setLineWidth(3);

  doc.setDrawColor(color);
  doc.circle(7.47, 8.47, 6.8);
  doc.setLineWidth(0);

  doc.setLineWidth(3);

  doc.setDrawColor(color);
  doc.circle(width - 7, 8.47, 6.8);
  doc.setLineWidth(0);

  const affiliateLines = doc.splitTextToSize(
    institute?.insAffiliation?.toUpperCase(),
    140
  );

  if (affiliateLines.length > 1) {
    let verticalOffset = 6;

    doc.setTextColor(55, 57, 74);
    doc.setFontSize(6.5);
    doc.text(width / 2, verticalOffset, affiliateLines, "center");
    verticalOffset = affiliateLines.length + 17;
  } else {
    let verticalOffset = 6;

    doc.setTextColor(55, 57, 74);
    doc.setFontSize(6.5);
    doc.text(width / 2, verticalOffset, affiliateLines, "center");
    verticalOffset = affiliateLines.length + 17;
  }

  const textLines = doc.splitTextToSize(
    institute.insName,

    40
  );

  if (textLines.length > 1) {
    let verticalOffset = affiliateLines.length > 1 ? 12.5 : 10;

    doc.setTextColor(73, 73, 227);
    doc.setFontSize(9);
    doc.text(width / 2, verticalOffset, textLines, "center");
    verticalOffset = textLines.length + 17;
  } else {
    let verticalOffset = affiliateLines.length > 1 ? 13.5 : 12;

    doc.setTextColor(73, 73, 227);
    doc.setFontSize(9);
    doc.text(width / 2, verticalOffset, textLines, "center");
    verticalOffset = textLines.length + 17;
  }

  const addressLines = doc.splitTextToSize(
    institute?.insAddress,

    115
  );

  if (addressLines.length > 1) {
    let verticalOffset = textLines.length > 1 ? 20 : 18;

    doc.setTextColor(26, 26, 46);
    doc.setFontSize(6);
    doc.text(width / 2, verticalOffset, addressLines, "center");
    verticalOffset = addressLines.length + 17;
  } else {
    let verticalOffset = textLines.length > 1 ? 20 : 18;

    doc.setTextColor(26, 26, 46);
    doc.setFontSize(6);
    doc.text(width / 2, verticalOffset, addressLines, "center");
    verticalOffset = addressLines.length + 17;
  }

  doc.setDrawColor(24, 24, 77);
  doc.setLineWidth(6);
  doc.line(0, 26, width, 26);

  const carryyText = user?.batchYear
    ? `Identity Card (${user?.batchYear})`
    : `Identity Card`;

  const carryTexttLines = doc.splitTextToSize(carryyText?.toUpperCase(), 50);

  doc.setTextColor(255, 255, 255);
  doc.setFontSize(8);

  if (carryTexttLines.length > 1) {
    let verticalOffset = 27;
    doc.text(width / 2, verticalOffset, carryTexttLines, "center");
    verticalOffset = carryTexttLines.length + 8;
  } else {
    let verticalOffset = 27;
    doc.text(width / 2, verticalOffset, carryTexttLines, "center");
    verticalOffset = carryTexttLines.length + 8;
  }

  doc.addImage(user.userProfilePhoto, "JPEG", 3, 32, 17, 19);

  doc.setTextColor(26, 26, 46);
  doc.setFontSize(7.5);
  doc.text(23, 35, `${user?.userName}`);
  doc.text(23, 39, `${user?.userDept}`);
  doc.text(23, 43, `${user?.userGRNumber} `);
  doc.setFontSize(5);
  doc.text(23, 47, `Issue Date: `);
  doc.setFontSize(7.5);
  doc.text(32, 47, `  ${user?.issueDate} `);
  doc.setFontSize(5);
  doc.text(23, 51, `Valid Upto: `);
  doc.setFontSize(7.5);
  doc.text(32, 51, `  ${user?.validDate} `);

  doc.setTextColor(26, 26, 46);
  doc.setFontSize(4.5);
  doc.text(
    12,
    height - 3,
    user?.userDesignation === "Student" ? `Student` : "Staff",
    "center"
  );
  doc.text(width - 12, height - 2.5, `Authorized Sign`, "center");

  doc.addImage(institute.signImg, "JPEG", width - 17, height - 10, 10, 5);

  // ----------------------------

  doc.addPage({
    orientation: "l",
    unit: "mm",
    format: [90, 60],
  });

  doc.setTextColor(44, 47, 55);
  doc.setFontSize(7);

  doc.text(5, 7, `Mobile Number: ${user?.userPhoneNumber}`);
  doc.text(5, 11, `DOB: ${user?.userMail}`);
  doc.text(5, 15, `DOB: ${user?.userDob}`);
  doc.text(5, 19, `Blood Group: ${user?.userBloodGroup}`);
  doc.text(5, 23, `PAN No. : ${user?.userPan}`);
  doc.text(5, 27, `Aadhar No. : ${user?.userAdhar}`);

  doc.addImage(barcodeimg, "JPEG", 5, 32, width - 10, 15);

  doc.setDrawColor(24, 24, 77);
  doc.setLineWidth(17);
  doc.line(0, 60, width, 60);

  doc.setTextColor(255, 255, 255);
  doc.setFontSize(6);

  const carryText =
    "This card is not transferable. If lost or found please inform to given address";

  const carryTextLines = doc.splitTextToSize(carryText, 85);

  if (carryTextLines.length > 1) {
    let verticalOffset = 56;
    doc.text(width / 2, verticalOffset, carryTextLines, "center");
    verticalOffset = carryTextLines.length + 8;
  } else {
    let verticalOffset = 56;
    doc.text(width / 2, verticalOffset, carryTextLines, "center");
    verticalOffset = carryTextLines.length + 8;
  }

  return doc.output("blob");
};
export default PdfIdCardLandscape;
