import jsPDF from "jspdf";

const PdfIdCardPortrait = (institute, user) => {
  var doc = new jsPDF({
    orientation: "p",
    unit: "mm",
    format: [60, 90],
    // putOnlyUsedFonts:true,
    // floatPrecision: 16 // or "smart", default is 16
  });
  var width = doc.internal.pageSize.getWidth();

  doc.setFont("Helvetica", "normal", "700");

  doc.addImage(institute.insLogo, "JPEG", 2, 3, 11, 11);
  doc.addImage(institute.affliatedImg, "JPEG", width - 13, 3, 11.5, 11.5);

  doc.setLineWidth(3);
  doc.setDrawColor(255, 255, 255);
  doc.circle(7.47, 8.47, 6.8);
  doc.setLineWidth(0);

  doc.setLineWidth(3);
  doc.setDrawColor(255, 255, 255);
  doc.circle(width - 7, 8.47, 6.8);
  doc.setLineWidth(0);

  const affiliateLines = doc.splitTextToSize(
    institute?.insAffiliation?.toUpperCase(),
    70
  );

  if (affiliateLines.length > 1) {
    let verticalOffset = affiliateLines.length > 2 ? 6 : 7.5;

    doc.setTextColor(227, 38, 54);
    doc.setFontSize(6.5);
    doc.text(width / 2, verticalOffset, affiliateLines, "center");
    verticalOffset = affiliateLines.length + 17;
  } else {
    let verticalOffset = affiliateLines.length > 2 ? 6 : 7.5;

    doc.setTextColor(227, 38, 54);
    doc.setFontSize(6.5);
    doc.text(width / 2, verticalOffset, affiliateLines, "center");
    verticalOffset = affiliateLines.length + 17;
  }

  const textLines = doc.splitTextToSize(
    institute.insName,

    30
  );

  if (textLines.length > 1) {
    let verticalOffset = textLines.length > 2 ? 16.4 : 17.5;

    doc.setTextColor(34, 74, 82);
    doc.setFontSize(9);
    doc.text(width / 2, verticalOffset, textLines, "center");
    verticalOffset = textLines.length + 17;
  } else {
    let verticalOffset = textLines.length > 2 ? 16.4 : 17.5;

    doc.setTextColor(34, 74, 82);
    doc.setFontSize(9);
    doc.text(width / 2, verticalOffset, textLines, "center");
    verticalOffset = textLines.length + 17;
  }

  // ------------------------------------

  const addressLines = doc.splitTextToSize(
    institute?.insAddress,

    65
  );

  if (addressLines.length > 1) {
    let verticalOffset = textLines.length > 2 ? 28 : 26.5;

    doc.setTextColor(18, 18, 18);
    doc.setFontSize(7);
    doc.text(width / 2, verticalOffset, addressLines, "center");
    verticalOffset = addressLines.length + 17;
  } else {
    let verticalOffset = textLines.length > 2 ? 29 : 26.5;

    doc.setTextColor(18, 18, 18);
    doc.setFontSize(7);
    doc.text(width / 2, verticalOffset, addressLines, "center");
    verticalOffset = addressLines.length + 17;
  }

  doc.addImage(user.userProfilePhoto, "JPEG", 21.5, 35, 18, 22);

  const nameLines = doc.splitTextToSize(
    user?.userName?.toUpperCase(),

    40
  );

  if (nameLines.length > 1) {
    let verticalOffset = 63;

    doc.setTextColor(34, 74, 82);
    doc.setFontSize(8.7);
    doc.text(width / 2, verticalOffset, nameLines, "center");
    verticalOffset = nameLines.length + 17;
  } else {
    let verticalOffset = 63;

    doc.setTextColor(34, 74, 82);
    doc.setFontSize(8.7);
    doc.text(width / 2, verticalOffset, nameLines, "center");
    verticalOffset = nameLines.length + 17;
  }

  const desigLines = doc.splitTextToSize(
    user?.userDesignation?.toUpperCase(),

    40
  );

  if (desigLines.length > 1) {
    let verticalOffset = 73;

    doc.setTextColor(8, 8, 8);
    doc.setFontSize(7.5);
    doc.text(width / 2, verticalOffset, desigLines, "center");
    verticalOffset = desigLines.length + 17;
  } else {
    let verticalOffset = 73;

    doc.setTextColor(8, 8, 8);
    doc.setFontSize(7.5);
    doc.text(width / 2, verticalOffset, desigLines, "center");
    verticalOffset = desigLines.length + 17;
  }

  doc.setDrawColor(34, 74, 82);
  doc.setLineWidth(17);
  doc.line(0, 90, width, 90);

  doc.setTextColor(255, 255, 255);
  doc.setFontSize(7);
  doc.text(width / 2 + 15, 86.5, "Authorized Sign", "center");

  doc.addPage({
    orientation: "p",
    unit: "mm",
    format: [60, 90],
    // putOnlyUsedFonts:true,
    // floatPrecision: 16 // or "smart", default is 16
  });

  doc.addImage(institute.insLogo, "JPEG", 2, 3, 11, 11);
  doc.addImage(institute.affliatedImg, "JPEG", width - 13, 3, 11.5, 11.5);
  doc.setLineWidth(3);
  doc.setDrawColor(255, 255, 255);
  doc.circle(7.47, 8.47, 6.8);
  doc.setLineWidth(0);

  doc.setTextColor(34, 74, 82);
  doc.setFontSize(10);
  doc.text(
    width / 2,
    20,
    user?.userDesignation === "Student" ? "Student Details" : "Staff Details",
    "center"
  );

  doc.setTextColor(44, 47, 55);
  doc.setFontSize(7.5);

  doc.text(5, 28, `Mobile Number: ${user?.userPhoneNumber}`);
  doc.text(5, 32, `DOB: ${user?.userMail}`);
  doc.text(5, 36, `DOB: ${user?.userDob}`);
  doc.text(5, 40, `Blood Group: ${user?.userBloodGroup}`);
  doc.text(5, 44, `PAN No. : ${user?.userPan}`);
  doc.text(5, 48, `Aadhar No. : ${user?.userAdhar}`);

  doc.setTextColor(34, 74, 82);
  doc.setFontSize(10);
  doc.text(width / 2, 56, "Address", "center");

  const userAddressLines = doc.splitTextToSize(
    user?.userAddress?.toUpperCase(),

    50
  );
  doc.setTextColor(44, 47, 55);
  doc.setFontSize(7.5);

  if (userAddressLines.length > 1) {
    let verticalOffset = 61;
    doc.text(width / 2, verticalOffset, userAddressLines, "center");
    verticalOffset = userAddressLines.length + 10;
  } else {
    let verticalOffset = 61;
    doc.text(width / 2, verticalOffset, userAddressLines, "center");
    verticalOffset = userAddressLines.length + 10;
  }
  doc.setTextColor(255, 0, 0);
  doc.setFontSize(6);

  const affiliateLines2 = doc.splitTextToSize(
    institute?.insAffiliation?.toUpperCase(),
    60
  );

  if (affiliateLines2.length > 1) {
    let verticalOffset = affiliateLines2.length > 1 ? 68 : 69;
    doc.text(width / 2, verticalOffset, affiliateLines2, "center");
    verticalOffset = affiliateLines2.length + 17;
  } else {
    let verticalOffset = affiliateLines2.length > 1 ? 68 : 69;
    doc.text(width / 2, verticalOffset, affiliateLines2, "center");
    verticalOffset = affiliateLines2.length + 17;
  }
  doc.setTextColor(0, 0, 0);
  doc.setFontSize(6);
  if (addressLines.length > 1) {
    let verticalOffset = 75;
    doc.text(width / 2, verticalOffset, addressLines, "center");
    verticalOffset = addressLines.length + 17;
  } else {
    let verticalOffset = 75;
    doc.text(width / 2, verticalOffset, addressLines, "center");
    verticalOffset = addressLines.length + 17;
  }

  doc.setDrawColor(34, 74, 82);
  doc.setLineWidth(17);
  doc.line(0, 88, width, 88);

  doc.setTextColor(255, 255, 255);
  doc.setFontSize(6);

  const carryText =
    "Always carry your card. In case of loss inform institute authority. If found please pass to given address";

  const carryTextLines = doc.splitTextToSize(carryText, 50);

  if (carryTextLines.length > 1) {
    let verticalOffset = 83;
    doc.text(width / 2, verticalOffset, carryTextLines, "center");
    verticalOffset = carryTextLines.length + 8;
  } else {
    let verticalOffset = 83;
    doc.text(width / 2, verticalOffset, carryTextLines, "center");
    verticalOffset = carryTextLines.length + 8;
  }

  // // let carryText
  // doc.text(5, 86.5, "Authorized Sign", "center");
  // doc.output("dataurlnewwindow", `Student-Bonafide.pdf`);

  return doc.output("blob");
};
export default PdfIdCardPortrait;
