import jsPDF from "jspdf";

const PdfIdCardPortrait2 = (institute, user) => {
  var doc = new jsPDF({
    orientation: "p",
    unit: "mm",
    format: [60, 90],
    // putOnlyUsedFonts:true,
    // floatPrecision: 16 // or "smart", default is 16
  });

  var width = doc.internal.pageSize.getWidth();
  var height = doc.internal.pageSize.getHeight();

  doc.setFont("Helvetica", "normal", "700");

  doc.setLineWidth(25);
  doc.setDrawColor(92, 158, 92);
  doc.circle(width / 2 + 20, height / 2 + 17, 77);
  doc.setLineWidth(0);

  doc.addImage(institute.affliatedImg, "JPEG", 1.5, 1.75, 11.8, 11.8);

  doc.setLineWidth(2.4);
  doc.setDrawColor(92, 158, 92);
  doc.circle(7.4, 7.7, 7.25);
  doc.setLineWidth(0);

  //   doc.setLineWidth(3);
  //   doc.setDrawColor(255, 255, 255);
  //   doc.circle(width - 7, 8.47, 6.8);
  //   doc.setLineWidth(0);

  const affiliateLines = doc.splitTextToSize(
    institute?.insAffiliation?.toUpperCase(),
    105
  );
  doc.setTextColor(67, 68, 77);
  doc.setFontSize(4.5);
  const textLines = doc.splitTextToSize(
    institute.insName,

    30
  );
  if (affiliateLines.length > 1) {
    let verticalOffset = textLines.length > 1 ? 4 : 5.5;
    doc.text(width / 2 + 10, verticalOffset, affiliateLines, "center");
    verticalOffset = affiliateLines.length + 17;
  } else {
    let verticalOffset = textLines.length > 1 ? 4 : 5.5;
    doc.text(width / 2 + 10, verticalOffset, affiliateLines, "center");
    verticalOffset = affiliateLines.length + 17;
  }

  if (textLines.length > 1) {
    let verticalOffset = affiliateLines.length > 1 ? 10.5 : 8.8;

    doc.setTextColor(207, 27, 51);
    doc.setFontSize(8);
    doc.text(width / 2 + 8, verticalOffset, textLines, "center");
    verticalOffset = textLines.length + 17;
  } else {
    let verticalOffset = affiliateLines.length > 1 ? 14 : 13;

    doc.setTextColor(207, 27, 51);
    doc.setFontSize(9);
    doc.text(width / 2 + 8, verticalOffset, textLines, "center");
    verticalOffset = textLines.length + 17;
  }

  //   // ------------------------------------

  const addressLines = doc.splitTextToSize(
    institute?.insAddress,

    65
  );
  doc.setTextColor(18, 18, 18);
  doc.setFontSize(6);

  if (addressLines.length > 1) {
    let verticalOffset = textLines.length > 1 ? 20 : 18.5;
    doc.text(width / 2 + 5, verticalOffset, addressLines, "center");
    verticalOffset = addressLines.length + 17;
  } else {
    let verticalOffset = textLines.length > 1 ? 19.5 : 18;
    doc.text(width / 2 + 5, verticalOffset, addressLines, "center");
    verticalOffset = addressLines.length + 17;
  }
  doc.setDrawColor(92, 158, 92);
  doc.setLineWidth(4.5);
  doc.line(10, 26, width - 10, 26);

  doc.setTextColor(255, 255, 255);
  doc.setFontSize(7.9);
  doc.text(width / 2, 27, `IDENTITY CARD ${user?.batchYear}`, "center");

  doc.addImage(institute.insLogo, "JPEG", 2, 34, 13, 13);
  doc.setLineWidth(3);
  doc.setDrawColor(255, 255, 255);
  doc.circle(8.47, 40.5, 7.6);
  doc.setLineWidth(0);
  doc.addImage(user.userProfilePhoto, "JPEG", 21.5, 29.5, 16.5, 20);
  doc.addImage(institute.affliatedImg, "JPEG", width - 15, 34, 13, 13);
  doc.setLineWidth(3);
  doc.setDrawColor(255, 255, 255);
  doc.circle(width - 8.47, 39.5, 7.6);
  doc.setLineWidth(0);

  doc.setTextColor(44, 47, 55);
  doc.setFontSize(8.5);

  doc.text(2, 55, `Name:`);

  const nameLines = doc.splitTextToSize(user?.userName, 45);

  if (nameLines.length > 1) {
    let verticalOffset = 55;
    doc.text(20, verticalOffset, nameLines, "left");
    verticalOffset = nameLines.length + 18;
  } else {
    let verticalOffset = 55;
    doc.text(20, verticalOffset, nameLines, "left");
    verticalOffset = nameLines.length + 18;
  }

  doc.setFontSize(7.3);

  if (nameLines.length > 1) {
    doc.text(2, 62, `Class:`);
    doc.text(20, 62, user?.userDept);
    doc.text(2, 66, `Blood Group:`);
    doc.text(20, 66, user?.userBloodGroup);
    doc.text(30, 66, `DOB:`);
    doc.text(38, 66, user?.userDob);
    doc.text(2, 70, `Mobile No:`);
    doc.text(20, 70, user?.userPhoneNumber);
    doc.text(2, 74, `Email:`);
    doc.text(20, 74, user?.userMail);
    doc.text(2, 78, `Address:`);

    const addressLines = doc.splitTextToSize(user?.userAddress, 45);

    if (addressLines?.length > 1) {
      let verticalOffset = 78;
      doc.text(20, verticalOffset, addressLines, "left");
      verticalOffset = addressLines?.length + 4;
    } else {
      let verticalOffset = 78;
      doc.text(20, verticalOffset, addressLines, "left");
      verticalOffset = addressLines?.length + 4;
    }
  } else {
    doc.text(2, 59, `Class:`);
    doc.text(20, 59, user?.userDept);
    doc.text(2, 63, `Blood Group:`);
    doc.text(20, 63, user?.userBloodGroup);
    doc.text(30, 63, `DOB:`);
    doc.text(38, 63, user?.userDob);
    doc.text(2, 67, `Mobile No:`);
    doc.text(20, 67, user?.userPhoneNumber);
    doc.text(2, 71, `Email:`);
    doc.text(20, 71, user?.userMail);
    doc.text(2, 75, `Address:`);
    const addressLines = doc.splitTextToSize(user?.userAddress, 45);

    if (addressLines?.length > 1) {
      let verticalOffset = 75;
      doc.text(20, verticalOffset, addressLines, "left");
      verticalOffset = addressLines?.length + 4;
    } else {
      let verticalOffset = 75;
      doc.text(20, verticalOffset, addressLines, "left");
      verticalOffset = addressLines?.length + 4;
    }
  }

  doc.setTextColor(44, 47, 55);
  doc.setFontSize(4.5);
  doc.text(
    12,
    height - 3,
    user?.userDesignation === "Student" ? `Student` : "Staff",
    "center"
  );
  doc.text(width - 12, height - 2.5, `Principal`, "center");

  doc.setLineWidth(0.2);
  doc.setDrawColor(0, 0, 0);
  doc.setFillColor(255, 255, 255);
  doc.rect(2.8, height - 7.5, 14, 5, "FD");
  institute.signImg &&
    doc.addImage(institute.signImg, "JPEG", width - 15.7, height - 8, 11, 5.6);

  return doc.output("blob");
};
export default PdfIdCardPortrait2;
